<template>
  <div id="home">
    <h1>用户注册</h1>
    <div>
      <input type="text" placeholder="昵称" class="textBox" @blur='detectBlurnickname' @focus='formInfo.nickName=""' v-model="nickName"/>
      <p class="info">{{formInfo.nickName}}</p>
    </div>
    <div>
      <input type="text" placeholder="用户名" class="textBox" @blur='detectBlurusername' v-model="userName" @focus='formInfo.userName=""'/>
      <p class="info">{{formInfo.userName}}</p>
    </div>
    <div>
      <input type="password" placeholder="密码" class="textBox" @change='handleChangepassword' @blur='detectBlurpassword'/>
      <p class="info">{{formInfo.password}}</p>
    </div>
    <div>
      <input type="password" placeholder="确认密码" class="textBox" @blur="detectBlurpasswords" @focus='detectFocuspassword'/>
      <p class="info">{{formInfo.passwords}}</p>
    </div>
    <div>
      <input type="text" placeholder="真实姓名" class="textBox" @blur="detectBlurname" v-model="name" @focus='formInfo.name=""'/>
      <p class="info">{{formInfo.name}}</p>
    </div>
    <div>
      <p>出生日期:</p><input type="number" max="2019" min="1919" @focus='focus' :value='date.year' @change='handleYearChange'/>年
      <input type="number" max="12" min="1" @focus='focus' @change='handleMonthChange' v-model="date.month"/>月
      <input type="number" max="31" min="1" @focus='focus' v-model="date.day" ref="maxDay"/>日
      <p class="info">{{formInfo.year}}</p>
    </div>
    <div>
      性别:<input type="radio" value='man' name="sex" id='man'/><label for='man'>男</label><input type="radio" value='woman' name="sex" id='woman'/><label for='woman'>女</label>
    </div>
    <div>
      <button @click='debounce(handleClickSubmit,5000)'>提交申请</button>
    </div>
  </div>
</template>

<script>
import Axios from 'axios'
// @ is an alias to /src
export default {
  name: 'home',
  data(){
    return {
      formInfo:{
        nickName:'',
        userName:'',
        password:'',
        passwords:'',
        name:''
      },
      password:'',
      nickName:'',
      userName:'',
      name:'',
      date:{
        year:'2000',
        month:'1',
        day:'1',
        maxDay:[31,28,31,30,31,30,31,31,30,31,30,31],
      },
    }
  },
  methods:{
    focus(e){
      e.target.blur()
    },
    handleChangepassword(e){
      this.password=e.target.value
    },
    detectBlurnickname(e){
      let v=e.target.value
      let reg=/[`~!@#$%^&*()_\-+=<>?:"{}|,.\/;'\\[\]·~！@#￥%……&*（）——\-+={}|《》？：“”【】、；‘'，。、]/im
      if(!v){
        this.formInfo.nickName='你的昵称没有填写'
        return
      }
      if(reg.test(v)){
        this.formInfo.nickName='昵称应该由中英文或数字组成'
        return
      }
    },
    detectBlurusername(e){
      let reg=/[\u4e00-\u9fa5]|[`~!@#$%^&*()_\-+=<>?:"{}|,.\/;'\\[\]·~！@#￥%……&*（）——\-+={}|《》？：“”【】、；‘'，。、]/im
      let v=e.target.value
      if(!v){
        this.formInfo.userName='你的用户名没有填写'
        return
      }
      if(reg.test(v)){
        this.formInfo.userName='用户名应该由英文或数字组成'
        return
      }
    },
    detectBlurpassword(e){
      if(e.target.value.length<6){
        this.formInfo.password='密码必须大于6位数'
      }
    },
    detectBlurpasswords(e){
      let v=e.target.value
      if(v!==this.password){
        this.formInfo.passwords='密码不一致'
      }
    },
    detectFocuspassword(e){
      this.formInfo.passwords=''
      e.target.value=''
    },
    detectBlurname(e){
      let v=e.target.value
      let reg=/[`~!@#$%^&*()_\-+=<>?:"{}|,.\/;'\\[\]·~！@#￥%……&*（）——\-+={}|《》？：“”【】、；‘'，。、]|[0-9]|[a-z]/im
      if(!v){
        this.formInfo.name='你的姓名没有填写'
        return
      }
      if(reg.test(v)){
        this.formInfo.name='姓名应该由中文组成'
        return
      }
    },
    handleClickSubmit(){
      let radios=[...document.querySelectorAll('[name=sex]')],radio,{year,month,day}=this.date,date,{nickName,userName,name,password}=this,sex
      for(let i=0,len=radios.length;i<len;i++){
        if(radios[i].checked){
          radio=radios[i]
          break
        }
      }
      if(!nickName){
        alert('您还没有填写昵称')
        return
      }
      if(!userName){
        alert('您还没有填写用户名')
        return
      }
      date=`${year}.${month}.${day}`
      if(!password){
        alert('您还没有填写密码')
        return
      }
      if(!name){
        alert('您还没有填写真实姓名')
        return 
      }
      if(!radio){
        alert('您的性别还没有选取')
        return
      }else{
        sex=radio.value
      }
      for(let i in this.formInfo){
        if(this.formInfo[i])return alert('表单未完成')
      }
      console.log(date)
      Axios.post('/User/submitUser',{
        params:{
          userName,
          nickName,
          name,
          password,
          sex,
          birthday:date
        }
      }).then((ok)=>{
        if(ok.data.err){
          this.formInfo.userName=ok.data.err.userName
        }else{
          alert('注册成功,点击确定跳转至首页')
          location.href=location.origin
        }
      })
    },
    handleMonthChange(e){
      this.$refs.maxDay.max=this.date.maxDay[e.target.value-1]
      this.date.day=1
      this.date.month=1
    },
    handleYearChange(e){
      let bur=this.calculationYear(e.target.value)
      this.date.year=e.target.value
      if(bur){
        this.date.maxDay=[31,29,31,30,31,30,31,31,30,31,30,31]
      }else{
        this.date.maxDay=[31,28,31,30,31,30,31,31,30,31,30,31]
      }
    },
    debounce(fn,wait){
    let timeout=Date.now()
    fn()
    return function(){
        if(timeout()-timeout>=wait){
            fn()
            timeout=Date.now()
        }
    }
    },
    calculationYear(value){
      let v=Number(value)
      if((v%4===0&&v%100!==0)||v%400===0){
        return true
      }
      return false
    }
  }
}
</script>
<style lang="scss">
#home{
  margin-top:4rem;
  width:40rem;
  background:white;
  margin:0 auto;
  color:#34353a;
  padding:1rem;
  box-sizing: border-box;
  >h1{
    margin-bottom:1rem;
  }
  >div{
    margin:1rem auto;
    width:15rem;
    text-align: left;
    >.info{
      color:red;
    }
    >.textBox{
      width:14rem;
      font-size:1rem;
      padding:0.4rem;  
    }
    >[type=number]{
      font-size: 0.8rem;
      width:3rem;
    }
    >[type=radio]{
      margin-left:1rem;
    }
    >button{
      width:100%;
      height:2rem;
    }
  }
}
</style>
